.review {
  flex-grow: 0;

  &__wrapper {
    padding-top: 19px;
    padding-right: 23px;
    padding-left: 25px;
  }

  &__list-item {
    align-items: center;

    &:not(:last-child) {
      margin-bottom: 0.5em;
    }
  }

  &__text {
    flex-shrink: 0;
  }

  &__dec-line {
    display: flex;
    flex-grow: 1;

    width: 100%;
    height: 1px;

    margin-left: 0.7em;
    margin-right: 0.7em;
    margin-top: 0.6em;

    background-color: $color-default-white;
  }
}
